import Validator from "js-object-validation";
import React, { Component } from "react";
import {
   Button,
   Card,
   CardBody,
   CardGroup,
   Col,
   Form,
   Input,
   InputGroup,
   InputGroupAddon,
   InputGroupText,
   Row,
   FormFeedback,
   FormGroup
} from "reactstrap";
import { logger } from "../../Helpers/Logger";
import { LoginValidations, LoginValidationsMessaages } from "../../validations";
class LoginPage extends Component {
   constructor(props) {
      super(props);
      this.state = {
         email: "",
         password: "",
         errors: {}
      };
   }
   handleChange = e => {
      const { target } = e;
      e.preventDefault();
      const { name, value } = target;
      this.setState({
         [name]: value,
         errors: {
            ...this.state.errors,
            [name]: false
         }
      });
   };
   login = e => {
      e.preventDefault();
      this.setState({
         errors: {}
      });
      try {
         const { isValid, errors } = Validator(
            this.state,
            LoginValidations,
            LoginValidationsMessaages
         );
         if (!isValid) {
            this.setState({
               errors,
               isLoading: false
            });
            return;
         }
         const { email, password } = this.state;
         this.props.onLogin({
            email,
            password
         });
      } catch (error) {
         logger(error);
      }
   };
   render() {
      const { email, password, errors } = this.state;
      return (
         <div className="app flex-row align-items-center auth-page  pt-3 pb-3">
            <div className="auth-bg" />

            <Row className="justify-content-center m-0 login">
               <Col md="12" lg="12" xl="12">
                  <Col className="text-center pb-3">
                     <h4 className="logo-title">
                        DEEP PLAY
                     </h4>
                  </Col>
                  <CardGroup>
                     <Card className="p-4 pl-4 pr-4">
                        <CardBody className="pl-4 pr-4 pt-0 pb-0">
                           <Form onSubmit={this.login}>
                              <h1 className="auth-title text-center">Admin Login</h1><br />
                              <FormGroup>
                                 <InputGroup className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                       <InputGroupText>
                                          <i className="icon-user" />
                                       </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                       type="text"
                                       placeholder="Email"
                                       autoComplete="username"
                                       name={"email"}
                                       value={email}
                                       onChange={this.handleChange}
                                       invalid={errors.email}
                                    />
                                    <FormFeedback>
                                       {errors.email ? errors.email : null}
                                    </FormFeedback>
                                 </InputGroup>
                              </FormGroup>
                              <FormGroup>
                                 <InputGroup className="mb-4">
                                    <InputGroupAddon addonType="prepend">
                                       <InputGroupText>
                                          <i className="icon-lock" />
                                       </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                       type="password"
                                       placeholder="Password"
                                       autoComplete="current-password"
                                       name={"password"}
                                       value={password}
                                       onChange={this.handleChange}
                                       invalid={errors.password}
                                    />
                                    <FormFeedback>
                                       {errors.password ? errors.password : null}
                                    </FormFeedback>
                                 </InputGroup>
                              </FormGroup>
                              <Row className={"m-0"}>
                                 <Col xs="8" className={"mt-0 mb-0 ml-auto mr-auto"}>
                                    <Button className="px-5 btn-theme" block>
                                       Login
                                    </Button>
                                 </Col>
                              </Row>
                           </Form>
                        </CardBody>
                     </Card>
                  </CardGroup>
               </Col>
            </Row>
         </div>
      );
   }
}
export default LoginPage;
